.container {
  display: block; }
  @media screen and (orientation: landscape) and (min-width: 800px) {
    .container {
      margin: 100px auto;
      width: max-content; } }
  @media screen and (orientation: portrait) and (max-width: 799px) {
    .container {
      margin: 20px; } }

.modal {
  margin: auto;
  position: relative;
  display: table-cell;
  min-width: 240px; }
  .modal .modalClose {
    color: var(--monet);
    position: absolute;
    right: 0;
    top: 0; }
    .modal .modalClose:hover {
      color: black; }
  .modal .modalHead {
    color: var(--monet);
    margin: 0 0 10px 0;
    font-weight: 700;
    display: inline-block; }
  .modal .pivot {
    padding-right: 4px; }

.menuContainer {
  display: table;
  padding: 10px; }
  .menuContainer > div {
    display: table-cell; }
  .menuContainer > div:not(:last-of-type) {
    padding-right: 20px; }
  .menuContainer h4 {
    margin: 0 0 10px 0; }
  .menuContainer .menuList {
    min-width: 100px;
    line-height: 1.8;
    display: table; }
    @media screen and (orientation: portrait) {
      .menuContainer .menuList {
        display: flex;
        flex-direction: column; }
        .menuContainer .menuList > div[data-list]:not(:last-of-type) {
          padding-right: 0;
          padding-bottom: 15px; } }
    .menuContainer .menuList > div {
      max-width: 140px; }
    .menuContainer .menuList > div:not([data-list]) {
      margin-bottom: 5px; }
    .menuContainer .menuList > div[data-list] {
      display: table-cell; }
      .menuContainer .menuList > div[data-list]:not(:last-of-type) {
        padding-right: 15px; }
      .menuContainer .menuList > div[data-list] h6 {
        color: var(--monet);
        font-size: .8rem;
        margin: 0 0 5px 0; }
      .menuContainer .menuList > div[data-list] p {
        margin: 0; }
